.page-footer {
  width: 320px;
  background-color: #f2f2f2;
}

.page-footer__navigation {
  display: block;
  width: 280px;
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 30px;
}

.page-footer__list {
  width: 280px;
  height: 65px;
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  border-top: 1px solid lightgrey;
  border-bottom: 1px solid lightgrey;
  padding-top: 25px;
  padding-bottom: 20px;
  box-sizing: border-box;
  padding-left: 75px;
  padding-right: 80px;
}

.page-footer__logo-link {
  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.page-footer__item {
  list-style: none;
}

.page-footer__social-link {
  text-decoration: none;
}

.page-footer__logo-image {
  display: block;
  width: 128px;
  height: 24px;
  margin: 0 auto;
  margin-top: 33px;
}

@media (min-width: 768px) {
  .page-footer,
  .page-footer__list {
    width: 768px;
  }

  .page-footer__list {
    width: 250px;
    border: none;
    padding-left: 27px;
    padding-right: 100px;
    padding-top: 20px;
    align-items: center;
  }

  .page-footer__logo-image,
  .page-footer__navigation {
    margin: 0;
  }

  .page-footer__logo-image {
    margin-right: 130px;
  }

  .page-footer__icon {
    &--vk {
      width: 30px;
      height: 16px;
    }

    &--inst {
      width: 23px;
      height: 23px;
    }

    &--fb {
      width: 14px;
      height: 28px;
    }

    &:hover {
      fill: #68b738;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

@media (min-width: 1200px) {
  .page-footer {
    position: relative;
    width: 1440px;
    margin-left: 124px;

    &--disabled {
      margin-top: -15px;
    }
  }
}
